.modal-wrap .modal-inner .total,
    .modal-wrap .modal-inner .error {
      font-size: 1.25em;
      margin: 15px 0;
}
.modal-wrap .modal-inner .total {
      color: green;
}
.modal-wrap .modal-inner .error {
      color: red;
}
.modal-wrap .modal-inner .red-border {
      border: 2px solid red;
}
@media (max-width: 700px) {
.modal-wrap .modal-image {
      display: none;
}
}
